/*----------  COOKIES AND POLICY PRIVACY  ----------*/
	
function setCookie(cname, cvalue, exdays) {
    let d = new Date();
    d.setTime(d.getTime() + (exdays * 24 * 60 * 60 * 1000));
    let expires = "expires="+d.toUTCString();
    document.cookie = cname + "=" + cvalue + ";" + expires + ";path=/";
}

function getCookie(cname) {
    let name = cname + "=";
    let ca = document.cookie.split(';');
    for(let i = 0; i < ca.length; i++) {
        let c = ca[i];
        while (c.charAt(0) == ' ') {
            c = c.substring(1);
        }
        if (c.indexOf(name) == 0) {
            return c.substring(name.length, c.length);
        }
    }
    return "";
}

function checkCookie() {
    document.getElementById("closeCookies").addEventListener("click", closeNotification);
    let visited = getCookie("visited");
    let checked = getCookie("checked");
    if (visited == "true" && checked == "true" ) {
        document.getElementById("cookienotification").style.display = "none"; 
    }
    else {
           document.getElementById("cookienotification").style.display = "block"; 
        setCookie("visited", "true", 365);
    }
} 

function closeNotification(e){
    e.preventDefault();
    setCookie("checked", "true", 365);  
    document.getElementById("cookienotification").style.display = "none"; 
}

checkCookie();

