$('.faq-question').on('click', function(){
    $('.faq-question').removeClass('active-question');
    $('.faq-answer').removeClass('show').addClass('hide');
    $('#faq-person').hide();
    var idQuestion = event.target.id; //cliked id
    $(this).addClass('active-question');
    var idQuestionNo = idQuestion.slice(-1); //number of id
    var matching =  $(".faq-answer[id$="+idQuestionNo+"]");
    matching.removeClass('hide').addClass('show');

    // console.log(event.target.id);
});